import React, { Component } from 'react';
import { Link } from "react-router-dom";

class Header extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div>
                 <Link to="/home">To Home Page</Link>
                 <Link to="/about">To About Page</Link>
            </div>
        )
    }

}

export default Header;